<template>
  <div class="section">
    <div class="tabs">
      <div :class="{active:tab == 1}" @click="tabs(1)">低泵冲</div>
      <div :class="{active:tab == 2}" @click="tabs(2)">地破及承压试验</div>
    </div>
    <Lay v-show="tab==1" :route="route" :arr="arr1" :url="url1" @click="tab(1)"/>
    <Lay v-show="tab==2" :route="route" :arr="arr2" :url="url2" @click="tab(2)"/>
  </div>
</template>

<script>
  import Lay from '@/components/common/template';
  import {floorburstandpressuretest,lowpumpspeed} from '@/config/record'
  export default {
    props: ["route"],
    data() {
      return {
        url1: '/data/query/formationfracturepressuredata',  //地破及承压
        arr1: floorburstandpressuretest,
        url2: '/data/query/lowpumppunchingtestdata',  //低泵冲
        arr2: lowpumpspeed,
        tab: 1
      }
    },
    components: {
      Lay
    },
    mounted: function () {},
    methods: {
      tabs(i) {
        console.log(i);
        if(this.tab==i) return;
        this.tab=i;
      }
    }
  }

</script>



